/* eslint-disable no-useless-escape */
import Heart from '../heart'
import BreedBtn from '../breed-btn'
import BreedSection from '../breed-section'
import mixin from '@/assets/mixin.js/mixin.js'
export default {
  name: 'front',
  mixins: [mixin],
  components: {
    Heart,
    BreedBtn,
    BreedSection
  },
  data () {
    return {
      isHidden: false,
      loading: true
    }
  },
  props: {
    dogsCart: {
      type: String
    }
  },
  methods: {
    addListBreeds () {
      this.isHidden = !this.isHidden
    }
  }
}
